<template>
  <div style="display: flex">
    <div style="width: 20%; height: 90vh; background-color: white">
      <h3 style="margin: 1em">超级企业列表({{ number }})</h3>
      <div style="display: flex; margin: 1em">
        <el-input
          type="text"
          placeholder="超级企业名称"
          v-model="search.ename"
          style="
            width: 80%;
            margin-right: 1em;
            border: 1px solid rgb(233, 233, 233);
            border-radius: 5px;
          "
        ></el-input>
        <el-button
          type="primary"
          style="
            width: 3em;
            height: 3em;
            display: flex;
            align-items: center;
            justify-content: center;
          "
          @click="openAddDialog"
        >
          <span
            class="el-icon-circle-plus-outline"
            style="font-size: 1.5em"
          ></span
        ></el-button>
      </div>
      <ul
        style="
          width: 105%;
          overflow-y: auto;
          max-height: 74vh;
          background-color: white;
        "
      >
        <li
          v-for="(item, index) in data"
          :key="index"
          @click="getOneBusiness(item.id)"
        >
          <div
            style="
              padding: 10px;
              border-bottom: 1px solid rgb(233, 233, 233);
              display: flex;
              align-items: center;
            "
            @mouseenter="changeBackgroundColor"
            @mouseleave="restoreBackgroundColor"
          >
            <div
              style="
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: rgb(56, 216, 143);
                margin-right: 10px;
                margin-left: 10px;
              "
            ></div>
            <div class="ellipsis">{{ item.ename }} ({{ item.num }})</div>
          </div>
        </li>
      </ul>
    </div>

    <!-- 添加 -->
    <el-dialog :visible.sync="dialogVisible" title="添加企业" width="40%">
      <el-form
        ref="addForm"
        :model="newBusiness"
        width="200px"
        label-width="100px"
        :rules="rules"
      >
        <el-form-item label="企业名称" prop="ename" class="add_label">
          <el-input v-model="newBusiness.ename" class="add_input"></el-input>
        </el-form-item>
        <el-form-item label="IP地址" prop="ipstr" class="add_label">
          <el-input v-model="newBusiness.ipstr" class="add_input"></el-input>
        </el-form-item>
        <el-form-item label="管理员姓名" prop="adminname" class="add_label">
          <el-input
            v-model="newBusiness.adminname"
            class="add_input"
          ></el-input>
        </el-form-item>
        <el-form-item label="管理员邮箱" prop="adminemail" class="add_label">
          <el-input
            v-model="newBusiness.adminemil"
            class="add_input"
          ></el-input>
        </el-form-item>
        <el-form-item label="最大数量" prop="maxnum" class="add_label">
          <el-input
            v-model.number="newBusiness.maxnum"
            class="add_input"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="最大并发数"
          prop="maxconcurrency"
          class="add_label"
        >
          <el-input
            v-model.number="newBusiness.maxconcurrency"
            class="add_input"
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="addBusiness">确认</el-button>
      </div>
    </el-dialog>

    <div style="width: 80%; height: 90vh; background-color: #f5f5f5">
      <div style="background-color: white; margin: 10px">
        <div
          style="width: 100%; height: 3.5em; display: flex; align-items: center"
        >
          <el-button-group style="margin-left: 10px">
            <el-button
              @click="handleButtonClick('/Super')"
              :type="getButtonType('/Super')"
              class="tab_button"
              >超级企业管理</el-button
            >
            <el-button
              @click="handleButtonClick('/Son')"
              :type="getButtonType('/Son')"
              >子企业管理</el-button
            >
          </el-button-group>
        </div>
        <div>
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getBusiness, addBusiness, getSearches } from "../../api/login_http";

export default {
  data() {
    return {
      data: [],
      number: 0,
      dialogVisible: false,
      newBusiness: {
        ename: "",
        ipstr: "",
        adminname: "",
        adminemil: "",
        maxnum: 0,
        maxconcurrency: 0,
      },
      rules: {
        ename: [{ required: true, message: "请输入企业名称", trigger: "blur" }],
        ipstr: [{ required: true, message: "请输入IP地址", trigger: "blur" }],
        adminname: [
          { required: true, message: "请输入管理员姓名", trigger: "blur" },
        ],
        adminemail: [
          { required: true, message: "请输入管理员邮箱", trigger: "blur" },
          { type: "email", message: "邮箱格式不正确", trigger: "blur" },
        ],
        maxnum: [
          { required: true, message: "请输入最大数量", trigger: "blur" },
          { type: "number", message: "请输入数字类型", trigger: "blur" },
        ],
        maxconcurrency: [
          { required: true, message: "请输入最大并发数", trigger: "blur" },
          { type: "number", message: "请输入数字类型", trigger: "blur" },
        ],
      },
      search: { ename: "" },
    };
  },
  mounted() {
    this.getBusiness();
  },
  watch: {
    "search.ename": function (newValue) {
      if (newValue != "") {
        this.getSearches();
      } else {
        this.getBusiness();
      }
    },
  },

  methods: {
    //获取企业列表
    getBusiness() {
      getBusiness().then((res) => {
        this.data = res.data.data;
        this.number = res.data.data.length;
        this.getOneBusiness(this.data[0].id);
      });
    },
    //打开添加弹窗
    openAddDialog() {
      this.dialogVisible = !this.dialogVisible;
    },
    //添加企业
    addBusiness() {
      addBusiness(this.newBusiness).then((res) => {
        if (res.msg == "超级企业添加成功") {
          this.$message({
            type: "success",
            message: "添加成功!",
          });
        } else {
          this.$message({
            message: res.msg,
            type: "warning",
          });
        }
        this.getBusiness();
      });
    },
    handleButtonClick(path) {
      if (this.$route.path !== path) {
        this.$router.push(path);
      }
    },
    getButtonType(path) {
      return this.$route.path === path ? "primary" : "default";
    },
    getOneBusiness(id) {
      console.log(id);
      this.$store.dispatch("updateParams", id);
      localStorage.setItem("id", id);
    },
    changeBackgroundColor(event) {
      // 在这里改变背景颜色
      event.target.style.backgroundColor = "rgba(64, 158, 255,0.3)"; // 例如改变为红色
    },
    restoreBackgroundColor(event) {
      event.target.style.backgroundColor = "white";
    },
    getSearches() {
      getSearches(this.search).then((res) => {
        this.data = res.data.data;
        this.number = res.data.data.length;
        this.getOneBusiness(this.data[0].id);
      });
    },
  },
};
</script>

<style>
.add_input {
  width: 25em;
  border: 1px solid rgb(233, 233, 233) !important;
  margin-left: -70px;
}

.add_label {
  display: flex;
}

.ellipsis {
  width: 90%;
  white-space: nowrap; /* 不换行 */
  overflow: hidden; /* 隐藏溢出部分 */
  text-overflow: ellipsis; /* 显示省略号 */
  width: 120px; /* 设置固定宽度 */
  text-align: left;
  margin-left: 0;
}

.el-message {
  min-width: 40px !important;
}
</style>
